<script>
  import pagination from '@/components/pagination/pagination.vue'
  import api from '../../api/api.js'

  export default {
    name:'list',
    data () {
      return {
//        loading: true,
        objectClass:['a','b','c','d'],
        level:['极度活跃','活跃','少量活跃','不活跃'],
        detail: '',          //搜索框内容
        pageSize:10,         //每页10条
        total:1,             //总条数
        pageCount:1,         //总页数
        page:1,              //当前第一页
        layout: "jumper, next, pager,prev",
        showHide0:true,
        showHide1:true,
        showHide2:true,
        showHide3:true,
        showHide4:false,
        showHide5:true,
        hasChoose: true,
        moreFlag0:true,
        moreFlag1:true,
        moreFlag2:true,
        span0:'',  //融资
        span1:'',  //成立年限
        span2:'',  //区域
        span3:'',  //一级行业
        span4:'',  //二级行业
        span5:'',  //标签
        value: '', //排序--下拉框选中项
//        registerCapital:'',  //融资情况
//        registerTime:'',  //注册时间
//        areaData:'',      //区域
//        primaryIndustry:'',  //一级
        twoLevelIndustry:['农业','林业','畜牧业','渔业','农、林、牧、渔服务业'], //二级
        options: [             //下拉框
          {value: '成立时间最短',label: '成立时间最短'},
          {value: '成立时间最长',label: '成立时间最长'},
          {value: '招聘活跃度最高',label: '招聘活跃度最高'},
          {value: '招聘活跃度最低',label: '招聘活跃度最低'}
        ],
        tableData:'',        //列表数据
        showPage:true,
        entName:this.$route.params.entName           //企业名称
      }
    },
    mounted(){
      this.detail=this.$route.params.entName;
      this.entName=this.$route.params.entName;

      this.initTable();
    },
    methods: {
      //初始化底部列表
      initTable(){
        let param={
          entName:this.entName,
          page:1,
          pageSize:this.pageSize
        };
        let _this = this;
        api.getListData(param)
          .then(res => {
            console.log('初始化返回的列表数据')
//            console.log(res)
            if(res.data.length>0){
//              _this.loading = false;
              _this.tableData = res.data; /*获取列表数据*/
              _this.total = res.totalRecord; /*总条数*/
              _this.pageCount = Math.ceil(_this.total/_this.pageSize); /*总页数*/
              _this.showPage = true  /*显示分页*/
            }else{
//              _this.loading = false;
//              _this.tableData = '暂无数据';
              _this.showPage = false
            }
          })
      },
      //点击搜索--底部列表--渲染
      searchPoint() {
        let param = {
          entName:this.entName,
          page:1,
          pageSize:this.pageSize
        };
        /*渲染底部列表*/
        let _this = this;
        api.getListData(param)
          .then(function(res){
            console.log('搜索返回的列表数据');
            console.log(res);
            if(res.data.length>0){
              _this.tableData = res.data; /*获取列表数据*/
              _this.total = res.totalRecord; /*总条数*/
              _this.pageCount = Math.ceil(_this.total/_this.pageSize); /*总页数*/
              _this.showPage = true  /*显示分页*/
            }else{
              _this.showPage = false
            }
          })
      },
      //清空搜索框
      clearValue() {
        this.detail = ''
      },
      //头部筛选--下拉框
      selectChange(){
        console.log(this.value);
        let params = {
          page:1,
          pageSize:this.pageSize,
          rankName: this.value
        };
        let _this = this;
        api.getRank(params)
          .then(res=>{
            console.log('排序筛选--返回的列表数据');
            console.log(res);
            if(res.data.length>0){
              _this.tableData = res.data;
              _this.total = res.totalRecord;
              _this.pageCount = Math.ceil(_this.total/_this.pageSize);
              _this.showPage = true;
            }else{
              _this.showPage = false;
            }
          })
      },
      //头部筛选--更多按钮
      moreBtnClick (n) {
        if (n === 0) {
          if (this.moreFlag0) {
            event.target.innerText = '收起';
            event.target.classList.add('active');
          } else {
            event.target.innerText = '更多';
            event.target.classList.remove('active');
          }
          this.moreFlag0 = !this.moreFlag0;
        } else if (n === 1) {
          if (this.moreFlag1) {
            event.target.innerText = '收起';
            event.target.classList.add('active');
          } else {
            event.target.innerText = '更多';
            event.target.classList.remove('active');
          }
          this.moreFlag1 = !this.moreFlag1;
        } else if (n === 2) {
          if (this.moreFlag2) {
            event.target.innerText = '收起';
            event.target.classList.add('active');
          } else {
            event.target.innerText = '更多';
            event.target.classList.remove('active');
          }
          this.moreFlag2 = !this.moreFlag2;
        }
      },
      //头部筛选--每个筛选条件选择
      searchLiClick (n) {
        if (this.hasChoose) {
          this.hasChoose = !this.hasChoose;
        }
        let liCon = event.target.innerText;
        let html = '<span>'+liCon+'</span><i>x</i>';
        let ulNode = document.getElementById('has-choose-con');
        let liNode = document.createElement("li");
        liNode.innerHTML = html;
        liNode.setAttribute("class", "li");
        ulNode.appendChild(liNode);
        if (n === 0){
          this.showHide0 = !this.showHide0;
          liNode.id='choose0';
          this.span0 = event.target.innerText;

            /*融资情况--筛选*/
            let _this = this;
            api.getFinancing({
              financing:this.span0,
              page:1,
              pageSize:this.pageSize
            })
            .then(res=>{
              console.log('融资情况筛选--返回的列表数据');
              console.log(res);
              if(res.data.length>0){
                _this.tableData = res.data;
                _this.total = res.totalRecord;
                _this.pageCount = Math.ceil(_this.total/_this.pageSize);
                _this.showPage = true;
              }else{
                _this.showPage = false;
              }
            })

        } else if (n === 1) {
          this.showHide1 = !this.showHide1;
          liNode.id='choose1';
          this.span1 = event.target.innerText;

            /*成立时间--筛选*/
            let _this = this;
            api.getEstablishData({
              years:this.span1,
              page:1,
              pageSize:this.pageSize
            })
            .then(res=>{
              console.log('成立时间筛选--返回的列表数据');
              console.log(res);
              if(res.data.length>0){
                _this.tableData = res.data;
                _this.total = res.totalRecord;
                _this.pageCount = Math.ceil(_this.total/_this.pageSize);
                _this.showPage = true;
              }else{
                _this.showPage = false;
              }
            })

        } else if (n === 2) {
            this.showHide2 = !this.showHide2;
            liNode.id='choose2';
            this.span2 = event.target.innerText;

            /*区域--筛选*/
            let _this = this;
            api.getAreaData2({
              province:this.span2,
              page:1,
              pageSize:this.pageSize
          })
            .then(res=>{
              console.log('区域筛选--返回的列表数据');
              console.log(res);
              if(res.data.length>0){
                _this.tableData = res.data;
                _this.total = res.totalRecord;
                _this.pageCount = Math.ceil(_this.total/_this.pageSize);
                _this.showPage = true;
              }else{
                _this.showPage = false;
              }
            })

        } else if (n === 3) {
          this.showHide3 = !this.showHide3;
          liNode.id='choose3';
          this.span3 = event.target.innerText;
          this.showHide4 = true;

            /*一级行业--筛选*/
            let _this = this;
            api.getPrimaryIndustry({
              industry:this.span3,
              page:1,
              pageSize:this.pageSize
            })
            .then(res=>{
              console.log('一级行业筛选--返回的列表数据');
              console.log(res);
              if(res.data.length>0){
                _this.tableData = res.data;
                _this.total = res.totalRecord;
                _this.pageCount = Math.ceil(_this.total/_this.pageSize);
                _this.showPage = true;
              }else{
                _this.showPage = false;
              }
            })

        } else if (n === 4) {
          this.showHide4 = !this.showHide4;
          liNode.id='choose4';
          this.span4 = event.target.innerText;

            /*二级行业--筛选*/
            let _this = this;
            api.getSecondaryIndustry({
              industry_e:this.span4,
              page:1,
              pageSize:this.pageSize
            })
            .then(res=>{
              console.log('二级行业筛选--返回的列表数据');
              console.log(res);
              if(res.data.length>0){
                _this.tableData = res.data;
                _this.total = res.totalRecord;
                _this.pageCount = Math.ceil(_this.total/_this.pageSize);
                _this.showPage = true;
              }else{
                _this.showPage = false;
              }
            })

        }else if(n === 5) {
          this.showHide5 = !this.showHide5;
          liNode.id='choose5';
          this.span5 = event.target.innerText;

            /*标签--筛选*/
            let _this = this;
            api.getLabel({
              label:this.span5,
              page:1,
              pageSize:this.pageSize
            })
            .then(res=>{
              console.log('标签筛选--返回的列表数据');
              console.log(res);
              if(res.data.length>0){
                _this.tableData = res.data;
                _this.total = res.totalRecord;
                _this.pageCount = Math.ceil(_this.total/_this.pageSize);
                _this.showPage = true;
              }else{
                _this.showPage = false;
              }
            })
        }
      },
      //头部筛选--清空 && 底部列表--渲染
      btnClear () {
        this.hasChoose = true;
        this.showHide0 = true;
        this.showHide1 = true;
        this.showHide2 = true;
        this.showHide3 = true;
        this.showHide5 = true;
        this.span0 = '';
        this.span1 = '';
        this.span2 = '';
        this.span3 = '';
        this.span4 = '';
        this.span5 = '';
//        this.showHide4 = true;
        let ulCon = document.getElementById('has-choose-con');
        while(ulCon.hasChildNodes())
        {
          ulCon.removeChild(ulCon.firstChild);
        }

        //底部列表--渲染
        this.initTable();
      },
      //头部筛选--关闭 && 底部列表--渲染
      xBtn (e) {
        if(e.target.nodeName == "I"||e.target.nodeName == "SPAN") {
          let id = e.target.parentNode.id;
          if (id == 'choose0') {
            this.showHide0 = true;
            this.span0 = '';
          } else if (id == 'choose1') {
            this.showHide1 = true;
            this.span1 = '';
          } else if (id == 'choose2') {
            this.showHide2 = true;
            this.span2 = '';
          } else if (id == 'choose3') {
            this.showHide3 = true;
            this.span3 = '';
            if (this.showHide4) {
              this.showHide4 = false;
            }
          } else if (id == 'choose4') {
            this.showHide4 = true;
            this.span4 = '';
          }else if (id == 'choose5') {
            this.showHide5 = true;
            this.span5 = '';
          }
          let parentNode = e.target.parentNode.parentNode;
          let liNode = e.target.parentNode;
          parentNode.removeChild(liNode);
          let length = parentNode.children.length;
          if (length === 0) {
            this.hasChoose = true;
          }

          //底部列表--渲染
          this.initTable();

        }
      },
      //底部列表--收藏
      favoriteBtn (item) {
        let tableEle = document.getElementsByClassName("tableConItem");
        let len = tableEle.length;
        let trNode = event.target.parentNode.parentNode;
        let popFavorite = document.getElementById("pop-favorite");
        let popCancelFavorite = document.getElementById("pop-cancel-favorite");
//        for ( let i =0; i < len; i++) {
//          tableEle[i].classList.remove('active');
//        }
//        trNode.classList.add('active');
        if (event.target.className) {
          //取消收藏
          let params = {
            id:item.id,
            collectStatus:0
          };
//          console.log(params);
          let _this = this;
          let ev = event;
          api.getCollect(params)
            .then(res=>{
              if(res.code === 1){
                let yScroll;
                let clientH = document.documentElement.clientHeight || document.body.clientHeight  ;

                if(yScroll === 0){ //无滚动
                  ev.target.classList.remove('active');
                  popFavorite.classList .remove('active');
                  popCancelFavorite.classList .add('active');
                }else{
                  if (document.documentElement && document.documentElement.scrollTop) {
                    yScroll = document.documentElement.scrollTop;
                  } else if (document.body) {
                    yScroll = document.body.scrollTop;
                  }
                  ev.target.classList.remove('active');
                  popFavorite.classList .remove('active');
                  popFavorite.style.top=(yScroll+clientH/2)+'px'

                  popCancelFavorite.style.top=(yScroll+clientH/2)+'px'
                  popCancelFavorite.classList .add('active');
                }

                setTimeout(function () {
                  popCancelFavorite.classList.remove('active')
                },2000)
              }
            })

        } else {
          //收藏
          let params = {
            id:item.id,
            collectStatus:1
          };
//          console.log(params);
          let _this = this;
          let ev = event;
          api.getCollect(params)
            .then(res=>{
              if(res.code === 1) {
                let yScroll;
                let clientH = document.documentElement.clientHeight || document.body.clientHeight;

                if (yScroll === 0) { //无滚动
                  ev.target.classList.add('active');
                  popFavorite.classList.add('active');
                  popCancelFavorite.classList.remove('active');
                } else { //有滚动
                  if (document.documentElement && document.documentElement.scrollTop) {
                    yScroll = document.documentElement.scrollTop;
                  } else if (document.body) {
                    yScroll = document.body.scrollTop;
                  }
                  ev.target.classList.add('active');
                  popFavorite.classList.add('active');
                  popCancelFavorite.classList.remove('active');

                  popFavorite.style.top = (yScroll + clientH / 2) + 'px'
                  popCancelFavorite.style.top = (yScroll + clientH / 2) + 'px'
                }

                setTimeout(function () {
                  popFavorite.classList.remove('active');
                }, 2000);
              }
            })
        }
      },
      //触发分页 && 底部列表--渲染
      pageChange (val) {
        this.page = val; //当前页
        let params = {
          entName:this.entName,
          page:this.page,
          pageSize:this.pageSize,
          financing: this.span0,
          years: this.span1,
          province: this.span2,
          industry: this.span3,
          industry_e: this.span4,
          label: this.span5,
          rankName: this.value
        };
        let _this = this;
        api.getListData(params)
          .then(res=>{
            _this.tableData = res.data;
            _this.page = res.page;
            _this.total = res.totalRecord;
            _this.pageCount = _this.total/_this.pageSize
          })
          console.log(`你给后台发送的数据是 当前是第${val}页 每页${this.pageSize}条`)
      },
      //列表--跳转详情页
      goToDetails(name) {
//        this.$router.push('/detail/'+id+'/'+name)
        this.$router.push({name:'detail',params: {entName:name}})
      },
      //返回首页
      btnBack () {
        this.$router.push('/home')
      },
      //头部筛选--导出数据
      sendData() {
        /*let params = {
          financing: this.span0,
          years: this.span1,
          province: this.span2,
         industry: this.span3,
         industry_e: this.span4,
          label: this.span5,
         rankName: this.value
        };
        let baseurl = 'http://192.168.20.110:8069/newReg/entList/sendData';
        location.href = baseurl+"?financing="+this.span0+"&years="+this.span1+"&province="+this.span2+"industry="+this.span3+"industry_e="+this.span4+"label="+this.span5+"rankName="+this.value
*/
      },
    },
    components: {
      pagination
    },
    filters: {
      isValue(val){
        return val = (val=="") ? "-" : val
      }
    }
  }
</script>

