<template>
  <div class="chartcon" :id='simpleLineId'></div>
</template>

<script>
  // 引入基本模板
  let echarts = require('echarts/lib/echarts')
  // 引入饼图组件
  require('echarts/lib/chart/line')
  require('echarts/lib/component/tooltip')
  require('echarts/lib/component/legend')
  export default {
    name: 'simpleLine',
    props:['simpleLineId','simpleLineData'],
    data(){
      return{

      }
    },
    mounted (){
      this.init(this.simpleLineId)
    },
    methods:{
      init(chartID) {
        console.log(this.simpleLineData)
        let detais=this.simpleLineData.currentMonth.theEnt;
        let detais2=this.simpleLineData.currentMonth.regAverage;
        let detais3=this.simpleLineData.currentMonth.indAverage;
        let statusArray=['上升','下降'];
console.log(detais)
        let myChart = echarts.init(document.getElementById(chartID));
        myChart.setOption({
          tooltip: {
            alwaysShowContent:true,
              backgroundColor:'transparent',
            trigger: 'axis',
            position:function(point, params, dom, rect, size){
              return ['100%', '20%'];
            },
            axisPointer:{
              type: 'line',
              lineStyle:{
                type:'dashed',
                color:'#e1e1e1'
              }
            },
            formatter: function(param){
                console.log(param)
              var html='';
             html +='<div style="position:relative;padding:12px;width:276px;height:110px;background:#f6f6f6;border-radius:5px;font-size:12px;color:#7a7a7a;line-height:20px;">'+
                '<p style="color:#70aaf8;font-size:12px;margin-bottom:10px;">招聘活跃度<span style="margin-left:40px;">'+param[0].name+'</span></p>'+
                '<p style="height:26px;">' +
                  '<span style="float:left; width:72px; color:#1a1a1a;">该企业</span> ' +
                  '<span style="font-size:16px;color:#0c92fe;font-weight:bold; margin-right:5px;min-width:30px;display: inline-block">'+param[0].value+'</span>' ;
                  if(param[0].value<500){
                      html+='<span style="border:1px solid #8ac1ff;background:rgba(0,80,255,.1);color:#008cff;display:inline-block;width:60px;height:16px;line-height:16px;font-size:12px;text-align: center;border-radius:2px;">'+'不活跃'+'</span>' ;
                  }else if(param[0].value>500 && param[0].value<700){
                     html+='<span style="border:1px solid #ffc74e;background:rgba(255,138,0,.1);color:#ed9c00;display:inline-block;width:60px;height:16px;line-height:16px;font-size:12px;text-align: center;border-radius:2px;">'+'少量活跃'+'</span>' ;
                  }else if(param[0].value>=700 && param[0].value<850){
                    html+='<span style="border:1px solid #4bdb82;background:rgba(90,255,138,.1);color:#00c057;display:inline-block;width:60px;height:16px;line-height:16px;font-size:12px;text-align: center;border-radius:2px;">'+'活跃'+'</span>' ;
                  }else if(param[0].value>=850 && param[0].value<1000){
                    html+='<span style="border:1px solid #ff9995;background:rgba(255,62,0,.1);color:#ff1800;display:inline-block;width:60px;height:16px;line-height:16px;font-size:12px;text-align: center;border-radius:2px;">'+'极度活跃'+'</span>' ;
                  }
              html+='<span style="height:26px;margin-left:10px;">' +'比上月<span>'+statusArray[detais.status]+' </span>'
                  if(statusArray[detais.status]==0){
                    html+='<span style="color:#ff9518;display: inline-block;min-width: 20px;">'+ detais.monthPercent+'</span>'+ '%</span>'
                  }else{
                    html+='<span style="color:#3aa7ff;display: inline-block;min-width: 20px;">'+ detais.monthPercent+'</span>'+ '%</span>'
                  }

              html+='</p>'+
                  '<p style="height:26px;">' +
                  '<span style="float:left; width:72px; color:#1a1a1a; ">区域平均值</span> ' +
                  '<span style="font-size:16px;color:#5fd331;font-weight:bold; margin-right:5px;min-width:30px;display: inline-block">'+param[1].value+'</span>';
                  if(param[1].value<500){
                    html+='<span style="border:1px solid #8ac1ff;background:rgba(0,80,255,.1);color:#008cff;display:inline-block;width:60px;height:16px;line-height:16px;font-size:12px;text-align: center;border-radius:2px;">'+'不活跃'+'</span>' ;
                  }else if(param[1].value>500 && param[1].value<700){
                    html+='<span style="border:1px solid #ffc74e;background:rgba(255,138,0,.1);color:#ed9c00;display:inline-block;width:60px;height:16px;line-height:16px;font-size:12px;text-align: center;border-radius:2px;">'+'少量活跃'+'</span>' ;
                  }else if(param[1].value>=700 && param[1].value<850){
                    html+='<span style="border:1px solid #4bdb82;background:rgba(90,255,138,.1);color:#00c057;display:inline-block;width:60px;height:16px;line-height:16px;font-size:12px;text-align: center;border-radius:2px;">'+'活跃'+'</span>' ;
                  }else if(param[1].value>=850 && param[1].value<1000){
                    html+='<span style="border:1px solid #ff9995;background:rgba(255,62,0,.1);color:#ff1800;display:inline-block;width:60px;height:16px;line-height:16px;font-size:12px;text-align: center;border-radius:2px;">'+'极度活跃'+'</span>' ;
                  }
              html+='<span style="height:26px;margin-left:10px;">' +'比上月<span>'+statusArray[detais2.status]+' </span>'
                  if(statusArray[detais.status]==0){
                    html+='<span style="color:#ff9518;display: inline-block;min-width: 20px;">'+ detais2.monthPercent+'</span>'+ '%</span>'
                  }else{
                    html+='<span style="color:#3aa7ff;display: inline-block;min-width: 20px;">'+ detais2.monthPercent+'</span>'+ '%</span>'
                  }

              html+='</p>'+
                '<p style="height:26px;">' +
                '<span style="float:left; width:72px; color:#1a1a1a; ">行业平均值</span> ' +
                '<span style="font-size:16px;color:#ff8f28;font-weight:bold; margin-right:5px;min-width:30px;display: inline-block">'+param[2].value+'</span>' ;
                if(param[2].value<500){
                  html+='<span style="border:1px solid #8ac1ff;background:rgba(0,80,255,.1);color:#008cff;display:inline-block;width:60px;height:16px;line-height:16px;font-size:12px;text-align: center;border-radius:2px;">'+'不活跃'+'</span>' ;
                }else if(param[2].value>500 && param[2].value<700){
                  html+='<span style="border:1px solid #ffc74e;background:rgba(255,138,0,.1);color:#ed9c00;display:inline-block;width:60px;height:16px;line-height:16px;font-size:12px;text-align: center;border-radius:2px;">'+'少量活跃'+'</span>' ;
                }else if(param[2].value>=700 && param[2].value<850){
                  html+='<span style="border:1px solid #4bdb82;background:rgba(90,255,138,.1);color:#00c057;display:inline-block;width:60px;height:16px;line-height:16px;font-size:12px;text-align: center;border-radius:2px;">'+'活跃'+'</span>' ;
                }else if(param[2].value>=850 && param[2].value<1000){
                  html+='<span style="border:1px solid #ff9995;background:rgba(255,62,0,.1);color:#ff1800;display:inline-block;width:60px;height:16px;line-height:16px;font-size:12px;text-align: center;border-radius:2px;">'+'极度活跃'+'</span>' ;
                }
              html+='<span style="height:26px;margin-left:10px;">' +'比上月<span>'+statusArray[detais3.status]+' </span>'
                if(statusArray[detais.status]==0){
                  html+='<span style="color:#ff9518;display: inline-block;min-width: 20px;">'+ detais3.monthPercent+'</span>'+ '%</span>'
                }else{
                  html+='<span style="color:#3aa7ff;display: inline-block;min-width: 20px;">'+ detais3.monthPercent+'</span>'+ '%</span>'
                }

              html+='</p>'+

                '</div>'
              return html
            }
          },
          grid: {
            top: '20%',
            left: '0%',
            right: '5%',
            bottom: '5%',
            containLabel: true,
          },
          legend: {
            data:['该企业','行业均值','区域平均值'],
            top:0,
            right:"0%",
            icon: 'circle',
            itemWidth:10,
            itemHeight:10,
            show:true
          },
          xAxis:  {
            type: 'category',
            boundaryGap: false,
            axisLine: {
              show:true,
              lineStyle: {
                color: '#b5c6e1'
              }
            },
            axisTick:{
              show:false
            },
            axisLabel: {
              margin: 10,
              textStyle: {
                fontSize: 12,
                color:'#b5c6e1'
              }
            },
            data: this.simpleLineData.chart.xAxisData
          },
          yAxis: {
            type: 'value',
            interval:200,
            axisTick: {
              show: false
            },
            name:'',
            axisLine: {
              show:false,
              lineStyle: {
                color: '#b5c6e1'
              }
            },
            nameTextStyle:{
              color:'#fff'
            },
            axisLabel: {
              margin: 10,
              textStyle: {
                fontSize: 12,
                color:'#92a4c0'
              }
            },
            splitLine: {
              show:true,
              formatter: '{value}',
              lineStyle: {
                color: ['#f4f7fb'],
                type: 'solid'
              }
            }

          },
          series: [
            {
              name:'该企业',
              type:'line',
              smooth: true,
              data:this.simpleLineData.chart.entLine,
              symbolSize:3,
              symbol:'emptyCircle',
              itemStyle: {
                normal: {
                  color: '#81ccfe',
                  borderWidth:2,
                  borderColor:'#3e8df5'
                }
              },

            },
            {
              name:'行业均值',
              type:'line',
              smooth: true,
              symbolSize:3,
              symbol:'emptyCircle',
              itemStyle: {
                normal: {
                  color: '#6ccd51',
                  borderWidth:2,
                  borderColor:'#97e7fd'
                }
              },
              data:this.simpleLineData.chart.indLine
            },
            {
              name:'区域平均值',
              type:'line',
              smooth: true,
              symbolSize:3,
              symbol:'emptyCircle',
              itemStyle: {
                normal: {
                  color: '#fc8c25',
                  borderWidth:2,
                  borderColor:'#fc8e29'
                }
              },
              data:this.simpleLineData.chart.regLine
            }
          ]
        });
        window.onresize = myChart.resize;
      }
    },
    watch:{
      simpleLineData(){
        this.init(this.simpleLineId)
      }
    }
  }
</script>

<style scoped>
  .chartcon{
    width: 50%;
    height: 100%;
  }
</style>



