<template>
  <div class="chartcon">
    <!--<iframe src="../../../static/html/key-word.html" style="width: 100%;height: 100%" scrolling="no" frameborder="0" name="myFrame"></iframe>-->
    <iframe src="./static/html/key-word2.html" ref="iframe" @load="loaded" style="width: 100%;height: 100%" scrolling="no" frameborder="0" name="myFrame"></iframe>
  </div>
</template>

<script>
  export default {
    name: 'keywordSecond',
    props:['words'],
    data(){
      return{
        colors:['#fd8b46','#fa995e','#f5ca61','#3472c5','#fa625e','#c0b9ea','#475e6f','#b7bf44','#d6f0fd','#8ca9da']
      }
    },
    mounted (){
//        console.log(this.$refs.iframe.contentWindow)
//        myFrame.window.init(this.words)
//        console.log( myFrame.window.init)
    },
    methods:{
      loaded(){
        for(let i=0; i<this.words.length; i++){
          this.words[i].itemStyle={ normal:{color:this.colors[i%10]}};
        }

        let keywordFn=this.$refs.iframe.contentWindow.init;
//        let keywordFn=this.$refs.iframe.contentWindow.vm.init;
        keywordFn(this.words);
      }
    },
    watch:{
      words(){
        this.loaded();
      }
    }
  };
</script>

<style scoped>
  .chartcon{
    width: 100%;
    height: 250px;
  }
</style>



