<template>
    <div class="chart-wrapper" :id='mapId' ref="chart">
    </div>
</template>

<script>
  // 引入基本模板
  let echarts = require('echarts/lib/echarts')
  import 'echarts/map/js/china.js';
    export default {
        name: 'jobMap',
        props:['mapId','jobMapData'],
        data() {
            return {}
        },
        mounted (){
          this.initChart(this.mapId)
        },
        methods: {
            initChart(chartID) {
//                console.log(this.jobMapData)
                let myChart = echarts.init(document.getElementById(chartID));
                myChart.setOption({
                    backgroundColor:'#f8fafe',
                    tooltip: {
                        trigger: 'item',
//                        show:true,
//                        padding:[10,18,10,18],
                          backgroundColor:'transparent',
                          formatter: function(param){
//                            console.log(param)
                            var html='';
                              return html +='<div style="margin-top:-100px;margin-left:-60px;position:relative;padding:6px 8px;background:#fff;border:1px solid #fa995e;border-radius:5px;font-size:14px;color:#7a7a7a;line-height:20px;box-shadow:0 0 10px 0 rgba(250,153,94,.4);">'+
                                '<p style="text-align:left;">'+param.data.name+'：</p>'+
                                '<p>招聘需求量：<span style="font-size:16px;color:#fa995e;font-weight:bold;">'+(param.data.value?param.data.value:"暂无数据")+'</span><span style="font-size:12px;">'+ (param.data.value?"个":"")+'</span></p>'+
                                '<i style="position:absolute;bottom:-23px;left:30px;width:0;height:0;border:12px solid transparent;border-top:11px solid #fa995e;"></i>'+
                                '<i style="position:absolute;bottom:-20px;left:32px;width:0;height:0;border:10px solid transparent;border-top:10px solid #fff;"></i>'+
                                '</div>';
                        },
                        textStyle: {
                            color: '#fff',
                            align:'center'
                        },
                    },
                    visualMap: {
                        left: 'left',
                        top: 'bottom',// 文本，默认为数值文本
//                        calculable: true,
                        padding:[0,0,0,40],
                        color:["#1d9cfc",'#c6e6fe'],
//                        color:["#2998FF",'#4EAAFF','#64C3FF','#CEE7FF'],
                        itemWidth :10,
                        itemHeight :100,
                        text: ['高','低'],
                        textStyle: {
                          color: '#999',
                        }
                    },
                    series: [
                      {
                          type: 'map',
                          mapType: 'china',
                          top:'0',
                          bottom:'0',
                          itemStyle: {
                              normal: {
                                  areaColor: '#C1E0FF',
                                  borderColor: '#7dcbfb',
                                  borderWidth: 1,
                                  borderType: 'solid',
                                  label: {
                                      show: false,
                                      textStyle: {
                                          color: '#000'
                                      }
                                  },
                              },
                              emphasis: {
                                  areaColor: '#fed23d',
                                  label: {
                                      show: true
                                  }
                              }
                          },
                          data:this.jobMapData,
                      }
                  ]
                });
                window.onresize = myChart.resize;
            }
        },
        watch: {
//          jobMapData: {
//              handler: function (val, oldVal) {
//                  this.myChart = this.$echarts.init(this.$refs.chart);
//                  this.initBar(this.mapId)
//              },
//              deep: true  //增加deep 观察对象的子对象变化
//            }
          jobMapData(){
            this.initChart(this.mapId)
          }
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
    .chart-wrapper{
        width:100%;
        height:100%;
    }
</style>



