<script type="text/javascript">
  import api from '../../api/api.js'
  import doubleBar from '@/components/echarts/doubleBar'
  import rosePieThird from '@/components/echarts/rosePie3'
  import rosePieFourth from '@/components/echarts/rosePie4'
