  export default {
    name: 'detail',
    data(){
      return {
        loading:true,
        entName:'',
        showTooltip:false,
        showTooltip2:false,
        showTooltip3:false,
        pageSize:10,
        total:1,
        pageCount:1,
        layout: "jumper, next, pager,prev",
        page:1,
        showPage:true,
        objectClass:['a','b','c','d'],
        level:['极度活跃','活跃','少量活跃','不活跃'],
        //头部招聘信息，招聘特点
        companyInfo:'',
        //招聘活跃度
        recruitActivity:'',
        //折线图--活跃度趋势图
        simpleLineData:'',
        //柱图--职位需求变化
        doubleBar: '',
        //饼图--学历分布
        pieDataEdu:'',
        //饼图--工作年限分布
        pieDataExp:'',
        experience:'',
        //饼图--工作薪酬分布
        pieDataSalary:'',
        salary:'',
        //地图--招聘分布地区
        activeMapData:'',
        //地图详情--职位增长最高和最低
        rankFirst:'',
        rankLast:'',
        //词云图--招聘职位关键词
        words:'',
        //招聘分析信息
        recruitAnalysis:'',
        //表格--招聘信息
        recruitData:'',
        //弹出框
        recruitInfo:false,
        recruitInfoDetails:''
      }
    },
    mounted () {
      this.entName = this.$route.params.entName;
      this.initChartData();
    },
    methods:{
      //初始化
      initChartData() {
        let params = {
          entName:this.entName
        };
        let params2 = {
          entName:this.entName,
          pageSize:this.pageSize,
          page:this.page
        };
        this.getDataList(params,params2);
      },
      /*所有图表数据*/
      getDataList (params,params2) {
        let _this = this;
        /*头部招聘信息，招聘特点*/
        api.getJobPoint(params)
          .then(res => {
//              console.log(res);
            _this.loading = false;
            _this.companyInfo = res.data;
            let str=res.data.percent.substr(0,res.data.percent.length-1);
            _this.companyInfo.percent=str;
          })
        /*招聘活跃度*/
        api.getInfoLiveness(params)
          .then(res => {
            _this.recruitActivity = res.data;
          })
        /*招聘活跃度--趋势折线图*/
        api.getInfoLivenessTrend(params)
          .then(res => {
            _this.simpleLineData = res.data;
          })
        /*职位需求变化--柱图*/
        api.getJobRequireChange(params)
          .then(res => {
            _this.doubleBar = res.data;
          })
        /*学历分布--饼图*/
        api.getEducationDis(params)
          .then(res => {
            _this.pieDataEdu = res.data;
          })
        /*工作年限分布--饼图*/
        api.getAgeDis(params)
          .then(res => {
            _this.pieDataExp = res.data;
            _this.experience = res.data.averagYears;
          })
        /*薪酬分布--饼图*/
        api.getSalaryDis(params)
          .then(res => {
            _this.pieDataSalary = res.data;
            _this.salary = res.data.averageSalary;
          })
        /*招聘分布地区-地图*/
        api.getRecruitDis(params)
          .then(res => {
            _this.activeMapData = res.data;
          })
        /*职位量增长最高的3个地区*/
        api.getFirstThree(params)
          .then(res => {
            _this.rankFirst = res.data;
          })
        /*职位量增长最低的3个地区*/
        api.getLastThree(params)
          .then(res => {
            this.rankLast = res.data;
          })
        /*招聘关键词*/
        api.getJobKey(params)
          .then(res => {
            _this.words = res.data;
          })
        /*招聘信息挖掘*/
        api.getInfoMining(params)
          .then(res => {
            this.recruitAnalysis = res.data;
          })
        /*招聘职位信息--列表*/
        api.getJobInfoList(params2)
          .then(res => {
            _this.recruitData = res.data;
            _this.total=res.totalRecord;
          })
      },
      //返回
      btnBack () {
        this.$router.push('/list')
      },
      // 鼠标移入问号，显示信息
      showIfon (){
        this.showTooltip = ! this.showTooltip
      },
      hideIfon(){
        this.showTooltip = ! this.showTooltip
      },
      showIfon2 (){
        this.showTooltip2 = ! this.showTooltip2
      },
      hideIfon2(){
        this.showTooltip2 = ! this.showTooltip2
      },
      showIfon3 (){
        this.showTooltip3 = ! this.showTooltip3
      },
      hideIfon3(){
        this.showTooltip3 = ! this.showTooltip3
      },
      //改变分页，渲染表格
      pageChange (val) {
        this.page = val; //当前页
        let params = {
          entName:this.entName,
          page:this.page,
          pageSize:this.pageSize
        };
        let _this = this;
        api.getJobInfoList(params)
          .then(res=>{
            _this.recruitData = res.data;
            _this.total = res.totalRecord;
            _this.pageCount = _this.total/_this.pageSize;
          })
        console.log(`你给后台发送的数据是：企业名字${this.entName} 当前是第${val}页 每页${this.pageSize}条`)
      },
      //招聘信息弹出框
      showRecruitInfo(position){
        let _this = this;
        let params={entName: position}
        api.getJobInfoDetails(params)
          .then(res => {
            _this.recruitInfo = true;
//            console.log(res.data);
            _this.recruitInfoDetails = res.data;
          })
      },
      //地图数据前三和后三
/*      rank(){
        let compare = function (prop) {
          return function (obj1, obj2) {
            let val1 = obj1[prop][2];
            let val2 = obj2[prop][2];
            if (!isNaN(Number(val1)) && !isNaN(Number(val2))) {
              val1 = Number(val1);
              val2 = Number(val2);
            }
            if (val1 > val2) {
              return -1;
            } else if (val1 < val2) {
              return 1;
            } else {
              return 0;
            }
          }
        }
        this.activeMapData.data2.sort(compare("value"));
        this.rankFirst.push(this.activeMapData.data2[0]);
        this.rankFirst.push(this.activeMapData.data2[1]);
        this.rankFirst.push(this.activeMapData.data2[2]);
        this.rankLast.push(this.activeMapData.data2[this.activeMapData.data2.length-1]);
        this.rankLast.push(this.activeMapData.data2[this.activeMapData.data2.length-2]);
        this.rankLast.push(this.activeMapData.data2[this.activeMapData.data2.length-3]);
      },*/
    },
    computed:{
    },
    components: {
      doubleBar,
      rosePieThird,
      rosePieFourth,
      rosePieFifth,
      distributeMap,
      keywordSecond,
      pagination,
      simpleLine
    }
  }
</script>

