    background-image:linear-gradient(to top, rgba(255,255,255,1) 0% , rgba(255,255,255,.5) 25%, rgba(255,255,255,.3) 50%, rgba(255,255,255,.2) 75%,rgba(255,255,255,.1) 100%);
