<template>
  <div class="chartcon" :id='barId'></div>
</template>

<script>
  // 引入基本模板
  let echarts = require('echarts/lib/echarts')
  // 引入饼图组件
  require('echarts/lib/chart/bar')
  require('echarts/lib/component/tooltip')
  require('echarts/lib/component/legend')
  export default {
    name: 'doubleBar',
    props:['barId','doubleBar'],
    data(){
      return{

      }
    },
    mounted (){
//      this.initBar(this.barId)
    },
    methods:{
      initBar(chartID) {
//        console.log(this.doubleBar)
        Array.prototype.sum = function (){
          var result = 0;
          for(var i = 0; i < this.length; i++) {
            result += this[i];
          }
          return result;
        };

        let sum=this.doubleBar.barDataTotal.sum();
        let myChart = echarts.init(document.getElementById(chartID));
        myChart.setOption({
          tooltip: {
            trigger: 'axis',
            axisPointer:{
              type:'shadow',
              z:0,
              shadowStyle: {
                color:'#f8f8f8'
              }
            },
            position:function(point, params, dom, rect, size){
              return [point[0], '10%'];
            },
            confine:true,
            backgroundColor:'transparent',
            formatter: function(param){
              var html='';
//              var sumArr=[];
//              console.log(param)
//              for(var i=0;i<param.length;i++){
//                sumArr.push(param[i].value);
//              }
//              console.log(param[i].value)
              return html +='<div style="position:relative;padding:6px 8px;background:#fff;border:1px solid #fa995e;border-radius:5px;font-size:12px;color:#7a7a7a;line-height:20px;box-shadow:0 0 10px 0 rgba(250,153,94,.4);">'+
                '<p>'+param[0].name+':</p>'+
                '<p>'+param[0].seriesName+': <span style="font-size:16px;color:#1ca4fc;font-weight:bold;">'+param[0].value+'</span></p>'+
                '<p>'+param[1].seriesName+': <span style="font-size:16px;color:#fa995e;font-weight:bold;">'+param[1].value+'</span></p>'+
                '<i style="position:absolute;bottom:-23px;left:30px;width:0;height:0;border:12px solid transparent;border-top:11px solid #fa995e;"></i>'+
                '<i style="position:absolute;bottom:-20px;left:32px;width:0;height:0;border:10px solid transparent;border-top:10px solid #fff;"></i>'+
                '</div>'
            }
          },
          backgroundColor:'#f8fafe',
          grid: {
            top:'20%',
            right:'8%',
            left:'12%',
            bottom:'9%'
          },
          legend: {
            textStyle: {
              fontSize: 14,
              color:'#183a6a'
            },
            top:'0%',
            right:'0%',
            data: [{
              name: '职位薪酬分布',
              icon: 'square'
            }],
            show:false
          },
          xAxis: [{
            type: 'category',
            axisLine: {
              lineStyle: {
                color: '#a4bae3'
              }
            },
            axisLabel: {
              textStyle: {
                color: '#7d93b5',
                fontSize: 12
              },
              interval:0
            },
            axisTick: {
              show: false
            },
            splitLine:{
              lineStyle:{
                color:'#d3def2'
              }
            },
            data: this.doubleBar.xAxisData
          }],
          yAxis: [
            {
              type: 'value',
              name: '',
              nameTextStyle: {
                color: '#7d93b5',
                fontSize: 12,
                padding:[0,40,0,0],
              },
              min: 0,
              splitLine:{
                lineStyle:{
                  color:'#e9eff8'
                }
              },
              axisTick: {
                show: false
              },
              axisLine: {
                lineStyle: {
                  color: '#b5c6e1'
                },
                show:false
              },
              axisLabel: {
                formatter: '{value}',
                textStyle: {
                  color: '#7d93b5',
                  fontSize: 12
                }
              }
            }
          ],
          series: [
            {
              name: '职位总数',
              type: 'bar',
              barWidth: 10,
              stack:'总量',
              itemStyle: {
                normal: {
                  color:new echarts.graphic.LinearGradient(
                    0, 0, 0, 1,
                    [
                      {offset: 0, color: '#1ca4fc'},
                      {offset: 1, color: '#1ca4fc'}
                    ]
                  )
                },
              },
              data: this.doubleBar.barDataTotal
            },
            {
              name: '新职位',
              type: 'bar',
              stack:'总量',
              barWidth: 10,
              itemStyle: {
                normal: {
                  color:new echarts.graphic.LinearGradient(
                    0, 0, 0, 1,
                    [
                      {offset: 0, color: '#ff9800'},
                      {offset: 1, color: '#ff9800'}
                    ]
                  )
                },
              },
              data: this.doubleBar.barDataNew
            }
          ]
        });
        window.onresize = myChart.resize;
      }
    },
    watch:{
      doubleBar(){
        this.initBar(this.barId)
      }
    }
  }
</script>

<style scoped>
  .chartcon{
    width: 100%;
    height: 100%;
  }
</style>



