<template>
  <div class="chartcon" :id='barLineId'></div>
</template>

<script>
  // 引入基本模板
  let echarts = require('echarts/lib/echarts')
  // 引入饼图组件
  require('echarts/lib/chart/bar')
  require('echarts/lib/chart/line')
  require('echarts/lib/component/tooltip')
  require('echarts/lib/component/legend')
  export default {
    name: 'barLine',
    props:['barLineId','recruitDemandChange'],
    data(){
      return{

      }
    },
    mounted (){
      this.initBar(this.barLineId)
    },
    methods:{
      initBar(chartID) {
//        console.log(this.recruitDemandChange)
        let myChart = echarts.init(document.getElementById(chartID));
        myChart.setOption({
          tooltip: {
            trigger: 'axis',
            axisPointer:{
              type:'shadow',
              z:0,
              shadowStyle: {
                color:'#f8f8f8'
              }
            },
            position:function(point, params, dom, rect, size){
////              return [point[0], '10%'];
//               return [, '10%'];
            },
            confine:true,
            backgroundColor:'transparent',
            formatter: function(param){
              var html='';
              return html +='<div style="position:relative;margin-top:-10px;margin-left:-70px;padding:6px 8px;background:#fff;border:1px solid #fa995e;border-radius:5px;font-size:12px;color:#7a7a7a;line-height:20px;box-shadow:0 0 10px 0 rgba(250,153,94,.4);">'+
                '<p>'+param[0].name+':</p>'+
                '<p>招聘职位需求量 <span style="font-size:16px;color:#7bbcf7;font-weight:bold;">'+param[0].value+'</span></p>'+
                '<p>招聘企业数量 <span style="font-size:16px;color:#fd7e23;font-weight:bold;">'+param[1].value+'</span></p>'+
                '<i style="position:absolute;bottom:-23px;left:30px;width:0;height:0;border:12px solid transparent;border-top:11px solid #fa995e;"></i>'+
                '<i style="position:absolute;bottom:-20px;left:32px;width:0;height:0;border:10px solid transparent;border-top:10px solid #fff;"></i>'+
                '</div>'
            }
          },
          backgroundColor:'#f8fafe',
          grid: {
            top:'40%',
            right:'6%',
            left:'6%',
            bottom:'14%'
          },
          legend: {
            textStyle: {
              fontSize: 14,
              color:'#183a6a'
            },
            top:'0%',
            right:'0%',
            data: [{
              name: '招聘职位需求量',
              icon: 'square'
            }, {
              name: '招聘企业数量',
              icon: 'emptyCircle',
            }]
          },
          xAxis: [{
            type: 'category',
            axisLine: {
              lineStyle: {
                color: '#b5c6e1'
              }
            },
            axisLabel: {
              textStyle: {
                color: '#b5c6e1',
                fontSize: 12
              }
            },
            axisTick: {
              show: false
            },
            splitLine:{
              lineStyle:{
                color:'#e9eff8'
              }
            },
            data: this.recruitDemandChange.xAxisData
          }],
          yAxis: [
            {
              type: 'value',
              name: '需求量（单位：万）',
              nameTextStyle: {
                color: '#7d93b5',
                fontSize: 12,
                padding:[0,0,0,50]
              },
              min: 0,
              interval: 50,
              splitLine:{
                lineStyle:{
                  color:'#e9eff8'
                }
              },
              axisTick: {
                show: false
              },
              axisLine: {
                lineStyle: {
                  color: '#b5c6e1'
                }
              },
              axisLabel: {
                formatter: '{value}',
                textStyle: {
                  color: '#7d93b5',
                  fontSize: 12
                }
              }
            },
            {
              type: 'value',
              name: '数量（单位：十万）',
              nameTextStyle: {
                color: '#7d93b5',
                fontSize: 12,
                padding:[0,30,0,0]
              },
              interval: 50,
              splitLine: {
                show: false
              },
              axisTick: {
                show: false
              },
              axisLine: {
                lineStyle: {
                  color: '#b5c6e1'
                }
              },
              axisLabel: {
                formatter: '{value}',
                textStyle: {
                  color: '#7d93b5',
                  fontSize: 12
                }
              }
            }
          ],
          series: [
            {
              name: '招聘职位需求量',
              type: 'bar',
              barWidth: 10,
              itemStyle: {
                normal: {
                  color:new echarts.graphic.LinearGradient(
                    0, 0, 0, 1,
                    [
                      {offset: 0, color: '#99ccfc'},
                      {offset: 1, color: '#5eabf4'}
                    ]
                  )
                },
              },
              data: this.recruitDemandChange.barData
            },
            {
              name: '招聘企业数量',
              type: 'line',
              lineStyle: {
                normal: {
                  color: '#eb9f73'
                }
              },
              symbolSize: 6,
              itemStyle: {
                normal: {
                  color: '#eb9f73',
                  borderWidth: 2,

                }
              },
              yAxisIndex: 1,
              data: this.recruitDemandChange.lineData
            }
          ]
        });
        window.onresize = myChart.resize;
      }
    },
    watch:{
      recruitDemandChange(){
        this.initBar(this.barLineId)
      }
    }
  }
</script>

<style scoped>
  .chartcon{
    width: 100%;
    height: 100%;
  }
</style>



