<template>
  <div class="chartcon" :id='pieId'></div>
</template>

<script>
  // 引入基本模板
  let echarts = require('echarts/lib/echarts')
  // 引入饼图组件
  require('echarts/lib/chart/pie')
  require('echarts/lib/component/tooltip')
  require('echarts/lib/component/legend')
  export default {
    name: 'rosePieFifth',
    props:['pieId','pieData'],
    data(){
      return{

      }
    },
    mounted (){
//      this.init(this.pieId)
    },
    methods:{
      init(chartID) {
//        console.log(this.pieData)
        let myChart = echarts.init(document.getElementById(chartID));
        myChart.setOption({
          tooltip: {
            show: true,
            backgroundColor:'transparent',
            position: ['27%','36%'],
            formatter:function(params){
//                console.log(params)
              var html='';
              return html += '<div style="width: 80px;height: 40px;text-align: center;margin-left:-16px;">' +
                '<p style="font-size:18px;text-align:center;color:'+params.color.colorStops[1].color+';">'+params.percent+'%</p>' +
                '<p style="font-size:12px;text-align:center;color:#575757;">'+params.name+'</p>' +
                '</div>'
            }
          },
          legend: {
            x: '10',
            bottom:0,
            itemHeight: 8,
            align: 'left',
            icon:"circle",
            textStyle: {
              color: '#999'
            },
            data:['0-5k','5-10k','10-15k','15-20k','20-30k','30k+']
          },
          series: [
            {
              z: 0,
              name: '',
              type: 'pie',
              center: ['38%', '46%'],
              radius: [79, 80],
              label:{
                normal:{
                  position:'center',
                  show: false
                }
              },
              labelLine: {
                normal: {
                  show: false
                }
              },
              data: [{
                value: 0,
                itemStyle: {
                  normal: {
                    color: '#e6e8ea'
                  }
                },
                tooltip:{show:false}
              }]
            },
            {
              name:'',
              type:'pie',
              radius: ['44', '66'],
              center: ['38%', '46%'],
              data:[
                {
                  value:this.pieData.salary[0].value,
                  name: '0-5k',
                  label:{
                    normal:{
                      textStyle:{
                        color:"#3fa7dc",
                        fontSize:18
                      },
                      show: false,
                      position: 'center'
                    }
                  },
                  itemStyle: {
                    normal: {
                      barBorderRadius: 20,
                      color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
                        offset: 0,
                        color: '#12b0f7'
                      }, {
                        offset: 1,
                        color: '#0c7af0'
                      }]),
                      shadowColor: 'rgba(0, 0, 0, 0.4)',
                      // shadowBlur: 10,

                    }
                  }
                },
                {
                  value:this.pieData.salary[1].value,
                  name: '5-10k',
                  label:{
                    normal:{
                      textStyle:{
                        color:"#3fa7dc",
                        fontSize:18
                      },
                      show: false,
                      position: 'center'
                    }
                  },
                  itemStyle: {
                    normal: {
                      barBorderRadius: 20,
                      color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
                        offset: 0,
                        color: '#17b3ff'
                      }, {
                        offset: 1,
                        color: '#139eff'
                      }]),
                      shadowColor: 'rgba(0, 0, 0, 0.4)',
                      // shadowBlur: 10,

                    }
                  }
                },
                {
                  value:this.pieData.salary[2].value,
                  name: '10-15k',
                  label:{
                    normal:{
                      textStyle:{
                        color:"#3fa7dc",
                        fontSize:18
                      },
                      show: false,
                      position: 'center'
                    }
                  },
                  itemStyle: {
                    normal: {
                      barBorderRadius: 20,
                      color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
                        offset: 0,
                        color: '#5ecaff'
                      }, {
                        offset: 1,
                        color: '#58c6ff'
                      }]),
                      shadowColor: 'rgba(0, 0, 0, 0.4)',
                      // shadowBlur: 10,

                    }
                  }
                },
                {
                  value:this.pieData.salary[3].value,
                  name: '15-20k',
                  label:{
                    normal:{
                      textStyle:{
                        color:"#3fa7dc",
                        fontSize:18
                      },
                      show: false,
                      position: 'center'
                    }
                  },
                  itemStyle: {
                    normal: {
                      barBorderRadius: 20,
                      color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
                        offset: 0,
                        color: '#fed600'
                      }, {
                        offset: 1,
                        color: '#fdb300'
                      }]),
                      shadowColor: 'rgba(0, 0, 0, 0.4)',
                      // shadowBlur: 10,

                    }
                  }
                },
                {
                  value:this.pieData.salary[4].value,
                  name: '20-30k',
                  label:{
                    normal:{
                      textStyle:{
                        color:"#3fa7dc",
                        fontSize:18
                      },
                      show: false,
                      position: 'center'
                    }
                  },
                  itemStyle: {
                    normal: {
                      barBorderRadius: 20,
                      color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
                        offset: 0,
                        color: '#60cb75'
                      }, {
                        offset: 1,
                        color: '#59c56f'
                      }]),
                      shadowColor: 'rgba(0, 0, 0, 0.4)',
                      // shadowBlur: 10,

                    }
                  }
                },
                {
                  value:this.pieData.salary[5].value,
                  name: '30k+',
                  label:{
                    normal:{
                      textStyle:{
                        color:"#3fa7dc",
                        fontSize:18
                      },
                      show: false,
                      position: 'center'
                    }
                  },
                  itemStyle: {
                    normal: {
                      barBorderRadius: 20,
                      color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
                        offset: 0,
                        color: '#ff8416'
                      }, {
                        offset: 1,
                        color: '#ff8416'
                      }]),
                      shadowColor: 'rgba(0, 0, 0, 0.4)',
                      // shadowBlur: 10,

                    }
                  }
                }
              ]
            }
          ]
        });
        window.onresize = myChart.resize;
      }
    },
    watch:{
      pieData(){
        this.init(this.pieId)
      }
    }
  }
</script>

<style scoped>
  .chartcon{
    width: 100%;
    height: 100%;
  }
</style>



