<template>
  <div class="chartcon" :id='pieId'></div>
</template>

<script>
  // 引入基本模板
  let echarts = require('echarts/lib/echarts')
  // 引入饼图组件
  require('echarts/lib/chart/pie')
  require('echarts/lib/component/tooltip')
  require('echarts/lib/component/legend')
  export default {
    name: 'rosePie',
    props:['pieId','pieData'],
    data(){
      return{
      }
    },
    mounted (){
//      this.init(this.pieId)
    },
    methods:{
      init(chartID) {
//        console.log(this.pieData)

        let myChart = echarts.init(document.getElementById(chartID));
        myChart.setOption({
          tooltip: {
            show: true,
            backgroundColor:'transparent',
            position: ['22%','30%'],
            formatter:function(params){
              var html='';
              return html += '<div style="width: 80px;height: 40px;text-align: center;margin-left:-20px;">' +
                '<p style="font-size:14px;text-align:center;color:'+params.color.colorStops[1].color+';">'+params.percent+'%</p>' +
                '<p style="font-size:12px;text-align:center;color:#575757;">'+params.name+'</p>' +
                '</div>'
            }
          },
          legend: {
            x: 'left',
            bottom:0,
            itemHeight: 8,
            align: 'left',
            icon:"circle",
            textStyle: {
              color: '#999'
            },
            data:['专科','本科','研究生','不限','其他']
          },
          series: [
            {
              z: 0,
              name: '',
              type: 'pie',
              center: ['37%', '42%'],
              radius: [31, 32],
              label:{
                normal:{
                  position:'center',
                  show: false
                }
              },
              labelLine: {
                normal: {
                  show: false
                }
              },
              data: [{
                value: 0,
                itemStyle: {
                  normal: {
                    color: '#dbdbdc'
                  }
                },
                tooltip:{show:false}
              }]
            },
            {
              name:'',
              type:'pie',
              radius: ['40', '55'],
              center: ['37%', '42%'],
              data:[
                {
                  value: this.pieData[0].value,
                  name: '专科',
                  label:{
                    normal:{
                      textStyle:{
                        color:"#3fa7dc",
                        fontSize:18
                      },
                      show: false,
                      position: 'center'
                    }
                  },
                  itemStyle: {
                    normal: {
                      barBorderRadius: 20,
                      color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
                        offset: 0,
                        color: '#589ee1'
                      }, {
                        offset: 1,
                        color: '#3a567d'
                      }]),
                      shadowColor: 'rgba(0, 0, 0, 0.4)',
                      // shadowBlur: 10,

                    }
                  }
                },
                {
                  value: this.pieData[1].value,
                  name: '本科',
                  label:{
                    normal:{
                      textStyle:{
                        color:"#3fa7dc",
                        fontSize:18
                      },
                      show: false,
                      position: 'center'
                    }
                  },
                  itemStyle: {
                    normal: {
                      barBorderRadius: 20,
                      color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
                        offset: 0,
                        color: '#faae80'
                      }, {
                        offset: 1,
                        color: '#f78d4a'
                      }]),
                      shadowColor: 'rgba(0, 0, 0, 0.4)',
                      // shadowBlur: 10,

                    }
                  }
                },
                {
                  value: this.pieData[2].value,
                  name: '研究生',
                  label:{
                    normal:{
                      textStyle:{
                        color:"#3fa7dc",
                        fontSize:18
                      },
                      show: false,
                      position: 'center'
                    }
                  },
                  itemStyle: {
                    normal: {
                      barBorderRadius: 20,
                      color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
                        offset: 0,
                        color: '#f59353'
                      }, {
                        offset: 1,
                        color: '#f9843c'
                      }]),
                      shadowColor: 'rgba(0, 0, 0, 0.4)',
                      // shadowBlur: 10,

                    }
                  }
                },
                {
                  value:this.pieData[3].value,
                  name: '不限',
                  label:{
                    normal:{
                      textStyle:{
                        color:"#3fa7dc",
                        fontSize:18
                      },
                      show: false,
                      position: 'center'
                    }
                  },
                  itemStyle: {
                    normal: {
                      barBorderRadius: 20,
                      color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
                        offset: 0,
                        color: '#86c3fd'
                      }, {
                        offset: 1,
                        color: '#5c9dd9'
                      }]),
                      shadowColor: 'rgba(0, 0, 0, 0.4)',
                      // shadowBlur: 10,

                    }
                  }
                },
                {
                  value: this.pieData[4].value,
                  name: '其他',
                  label:{
                    normal:{
                      textStyle:{
                        color:"#3fa7dc",
                        fontSize:18
                      },
                      show: false,
                      position: 'center'
                    }
                  },
                  itemStyle: {
                    normal: {
                      barBorderRadius: 20,
                      color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
                        offset: 0,
                        color: '#60cb75'
                      }, {
                        offset: 1,
                        color: '#59c56f'
                      }]),
                      shadowColor: 'rgba(0, 0, 0, 0.4)',
                      // shadowBlur: 10,

                    }
                  }
                }]
            }
          ]
        });
        window.onresize = myChart.resize;
      }
    },
    watch:{
      pieData(){
        this.init(this.pieId)
      }
    }
  }
</script>

<style scoped>
  .chartcon{
    width: 100%;
    height: 100%;
  }
</style>



