<template>
  <div class="chartcon" :id='singleBarId' style="margin-top: -30px;"></div>
</template>

<script>
  // 引入基本模板
  let echarts = require('echarts/lib/echarts')
  // 引入饼图组件
  require('echarts/lib/chart/bar')
  require('echarts/lib/component/tooltip')
  require('echarts/lib/component/legend')
  export default {
    name: 'singleBar',
    props:['singleBarId','topTenBar','barMaxData'],
    data(){
      return{
        max:200,
      }
    },
    mounted (){
      this.initBar(this.singleBarId)
    },
    methods:{
      initBar(chartID) {
//        console.log(this.barMaxData)
        var seriesData = [];
        seriesData.push({
          name:this.topTenBar.industry,
          value:this.topTenBar.value
        })
        let myChart = echarts.init(document.getElementById(chartID));
        myChart.setOption({
          grid:{
            bottom:30,
            left:0,
            right:90
          },
          yAxis: [
          {
            data: [],
            axisLabel: {
              textStyle: {
                color: '#8eb5ef',
                fontSize:16,
                align:'left',
              }
            },
            offset:150,
            splitLine: {
              show: false
            },
            axisTick:{
              show:false
            },
            axisLine: {
              show: false
            }
          }],
          xAxis: {
            max: 150,
            show:false,
            axisLine: {
              show: false
            }
          },
          color:['#65b0f5'],
          series: [
          {
            type: 'bar',
            barGap:'-100%',
            silent: true,
            itemStyle: {
              normal: {
                barBorderRadius: 20,
                color: '#cee6fe',

              }
            },
            label: {
//              normal: {
//                position: [250,-3],
//                show:true,
//                formatter:function(value){
//
//
//                },
//                color:'#6c7b97'
//              }
            },
            barWidth: 6,
            data: [this.barMaxData]
          },
          {
            type: 'bar',
            data: seriesData,
            barWidth: 6,
            label: {
              normal: {
                position: [255,-3],
                show:true,
                formatter: function(value){
//                    console.log(value)
                  return value.value;
                },
                color:'#6c7b97'
              }
            },
            itemStyle: {
              normal: {
                barBorderRadius: 20,
                shadowColor: '#63aff4',
              }
            }
          }]
        });
        window.onresize = myChart.resize;
      }
    },
    watch:{
      barMaxData(){
        this.initBar(this.singleBarId)
      },
      topTenBar(){
        this.initBar(this.singleBarId)
      }
    }
  }
</script>

<style scoped>
  .chartcon{
    width: 100%;
    height: 100%;
  }
</style>



