  <script type="text/javascript">
    import api from '../../api/api.js'
    import Selects from '@/components/select/select.vue'
    import barLine from '@/components/echarts/barLine'
    import jobMap from '@/components/echarts/jobMap'
    import activeMap from '@/components/echarts/activeMap'
    import singleBar from '@/components/echarts/singleBar'
    import rosePie from '@/components/echarts/rosePie'
    import rosePieSec from '@/components/echarts/rosePie2'
    import simpleBar from '@/components/echarts/simpleBar'
    import keyword from '@/components/echarts/keyword'
    export default {
      name: 'home',
      data(){
        return {
          showTooltip:false,
          detail: '',
          area: '全国',
          industry: '全部行业',
          date: '1周',
          num: 0,
          //招聘活跃度
          recruitLiveness:'',
          arrowClass:['arrowUp','arrowDown'],
          //招聘职位需求量
          recruitJobCount:'',
          recruitEntCount:'',
          //折线柱图--职位需求变化
          recruitDemandChange:'',
          //地图1数据--职位需求量
          jobMapData: [],
          jobMapRankFirst:[],
          //地图2数据--招聘活跃度
          activeMapData: [],
          activeMapRankFirst:[],
          classArr:['first','second','third'],
          //横向柱图--top10
          topTenBarData:[],
          //横向柱图--最大值
          barMaxData:'',
          //饼图--学历要求，工作年限要求
          pieData:[],
          pieData2:'',
          experience:'',
          //柱图--职位薪酬分布
          simpleBar:'',
          salary:'',
          //词云图--招聘职位关键词
          words:''
        }
      },
      mounted () {
        this.initData();
      },
      methods:{
        //获取子组件传的参数
        gettimes(msg) {

          this.timeDuring = msg
          if(msg.page === 0) {
//            console.log(msg.text)
            this.area = msg.text;
          }else if(msg.page === 1){
//            console.log(msg.text)
            this.industry = msg.text
          }else if(msg.page === 2){
//            console.log(msg.text)
            this.date = msg.text;
          }
          this.initData();
        },
        initData() {
          let params={
            regional:this.area,
            industry:this.industry,
            timeHorizon:this.date
          };
//          console.log(params)
          this.getDataList(params);
        },
        getDataList (params) {
          /*招聘活跃度*/
          api.getRecruitLiveness(params)
            .then(res => {
              this.recruitLiveness = res.data;
            })
          /*招聘职位需求量,招聘企业数*/
          api.getRecruitJobCount(params)
            .then(res => {
              let str=res.data.jobCount.substr(0,res.data.jobCount.length-2);
              let str2=res.data.entCount.substr(0,res.data.entCount.length-2);
              this.recruitJobCount = str;
              this.recruitEntCount = str2;
            })
          /*招聘需求变化--柱图折线图*/
          api.getRecruitDemandChange(params)
            .then(res => {
              this.recruitDemandChange = res.data.needsChange;
            })
          /*职位需求量，招聘活跃度--地图*/
          api.getRegionalJobCount()
            .then(res => {
              /*职位需求量*/
              this.jobMapData = res.data.jobCount;
              /*招聘活跃度*/
              this.activeMapData = res.data.recruitLiveness;
              /*职位需求量--前三*/
              this.jobMapRankFirst.length = 0; //先清空
              this.jobMapData.sort(this.compare("value"));
              this.jobMapRankFirst.push(this.jobMapData[0]);
              this.jobMapRankFirst.push(this.jobMapData[1]);
              this.jobMapRankFirst.push(this.jobMapData[2]);
              /*职位需求量--前三*/
              this.activeMapRankFirst.length = 0; //先清空
              this.activeMapData.sort(this.compare("value"));
              this.activeMapRankFirst.push(this.activeMapData[0]);
              this.activeMapRankFirst.push(this.activeMapData[1]);
              this.activeMapRankFirst.push(this.activeMapData[2]);
            })
          /*所在行业Top10--柱图*/
          api.getIndustryTop10(params)
            .then(res => {
              this.topTenBarData = res.data;
              this.topTenBarData.sort(this.compare("value"));
              this.barMaxData = parseInt(this.topTenBarData[0].value)+20;
            })
          /*学历要求*/
          api.getEducationBackground(params)
            .then(res => {
              this.pieData = res.data;
            })
          /*工作经验要求*/
          api.getJobExperience(params)
            .then(res => {
              this.pieData2 = res.data.years;
              this.experience = res.data.averagYears?res.data.averagYears:'暂无'
            })
          /*薪酬分布*/
          api.getSalaryDistribution(params)
            .then(res => {
              this.simpleBar = res.data.salary;
              this.salary = res.data.averageSalary;
            })
          /*招聘关键词*/
          api.getRecruitKeyword(params)
            .then(res => {
              this.words = res.data;
            })
        },
        //点击搜索-跳转至list页码
        searchPoint() {
          if(!this.detail){
              this.$alert('请输入关键词...', '提示', {
                confirmButtonText: '确定',
//                callback: action => {
//                  this.$message({
//                    type: 'info'
//                  });
//                }
              });
            }else{
              this.$router.push({name:'list',params: { entName:this.detail }})
            }
        },
        //清空搜索
        clearValue() {
          this.detail = ''
        },
        //地图切换
        switchs(ind) {
          if(ind == 0){
            this.num = 0;
  //          this.$router.push({path: '/index'})
          }else{
            this.num = 1;
  //          this.$router.push({path: '/analysis'})
          }
        },
        // 鼠标移入问号，显示信息
        showIfon (){
          this.showTooltip = ! this.showTooltip;
        },
        hideIfon(){
          this.showTooltip = ! this.showTooltip;
        },
        /*排序封装*/
        compare(prop){
          return function (obj1, obj2) {
            let val1 = obj1[prop];
            let val2 = obj2[prop];
            if (!isNaN(Number(val1)) && !isNaN(Number(val2))) {
              val1 = Number(val1);
              val2 = Number(val2);
            }
            if (val1 > val2) {
              return -1;
            } else if (val1 < val2) {
              return 1;
            } else {
              return 0;
            }
          }
        }
      },
      components: {
        Selects,
        barLine,
        activeMap,
        jobMap,
        singleBar,
        rosePie,
        rosePieSec,
        simpleBar,
        keyword
      }
     }
  </script>

